.App {
  min-height: 100vh;
  text-align: center;
  background-color: #282c34;
  color: white;
}


.Container{
  padding: 100px;
}

.Shelf{
  flex: 1;
  display: grid;
  border: white solid 2px;
  grid-template-columns: repeat(8, minmax(100px, 1fr));
  grid-auto-rows: minmax(100px, auto);
}

.Label{
  border: solid 1px white;
  display: flex;
  flex-direction: column;
  font-size: 12px;
  width:auto;
  color: white;
}

.LabelImage{
  max-width:100%;
  height:auto;
}

.Unknown{
  display: flex;
  flex-direction: row;
  max-width: 100%;
}

.UnknownCell{
  max-width: 200px;
  margin-right: 20px;
}

/*Not needed*/

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
